:root {
  --light-purple: #FF85BF;
  --dark-purple: #BF4982;
  --beige: #FFF7B8;
  --light-green: #54A6B3;
  --dark-green: #27555C;
  --invalid: #F59393;
  --white: #FFFFFF;
  --black: #222121;
  --gray: #fefefe;
}

.flex-center {
  display: flex;
  align-items: center;
}

.flex-center-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.bg-dark-green {
  background: var(--dark-green);
}

.bg-light-green {
  background: var(--light-green);
}

.bg-dark-purple {
  background: var(--dark-purple);
}

.bg-light-puple {
  background: var(--light-puple);
}

@font-face {
  font-family: "inter-light";
  src: url("../fonts/inter-light.woff") format("woff"), url("../fonts/inter-light.woff2") format("woff2"), url("../fonts/inter-light.eot") format("eot"), url("../fonts/inter-light.otf") format("otf"), url("../fonts/inter-light.ttf") format("ttf");
}
@font-face {
  font-family: "inter";
  src: url("../fonts/inter.woff") format("woff"), url("../fonts/inter.woff2") format("woff2"), url("../fonts/inter.eot") format("eot"), url("../fonts/inter.otf") format("otf"), url("../fonts/inter.ttf") format("ttf");
}
* {
  font-family: "inter-light";
  outline: none;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  background: var(--gray);
}